<div class="hello"><div class="ui container center aligned grid"><div class="sixteen wide column" v-if="!uid &amp;&amp; (!users || toList(users).length == 0)"><div class="ui huge buttons center aligned"><button class="ui orange button" @click="toggleLogin"><i class="google icon"></i>{{ $t('login.login_to_see_data') }}</button></div></div><loader v-if="users &amp;&amp; toList(users).length &gt; 0 &amp;&amp; groups[$route.params.idx] &amp;&amp; !groups[$route.params.idx].hidden"><div class="ui center aligned grid"><div class="ui column center aligned" ui-if="groups[$route.params.idx].n"><div class="ui raised segment center aligned" style="margin-top: 30px; max-width: 800px;"><h3 class="ui header">〈{{groups[$route.params.idx].n}}〉</h3><p class="center aligned">{{groups[$route.params.idx].intro}}<br class="thin-only"><a @click="toggleEdit($route.params.idx)" v-if="isMember(groups[$route.params.idx].idx)"><i class="edit icon"></i>{{edit ? $t('login.end') : ''}}{{ $t('group.edit_group') }}</a></p><router-link class="ui basic green button" to="/groups"><i class="globe icon"></i>{{$t('group.explore_groups')}}</router-link><div class="ui form" v-show="edit"><div class="field" v-if="!uid"><button class="ui orange button" @click="loginGoogle()"><i class="google icon"></i>{{$t('login.login_first')}}</button></div><div class="field"><div class="ui labeled input"><label class="ui label">{{$t('group.enter_intro')}}</label><textarea v-model="newIntro" @input="filterInput('newIntro', $event)" :placeholder="$t('group.enter_intro_first')" rows="5" style="width: 100%; font-size: 18px;"></textarea></div></div><div class="field"><a class="ui green button" :class="{disabled: !newIntro}" @click="addIntro($route.params.idx)">{{$t('group.update_intro')}}</a></div></div><p v-if="(groups[$route.params.idx].members || []).length === 0"><button class="ui red button" @click="hideGroup($route.params.idx)"><i class="delete icon"></i>{{ $t('group.delete_group') }}</button></p><div class="ui grid center aligned"><div class="row"><p class="center aligned">成員：<span v-for="m in groups[$route.params.idx].members"><router-link :to="'/flag/' + m" v-if="isUser(m)"><img class="ui avatar" :src="users[m].photoURL" :alt="users[m].n"></router-link></span><span v-if="uid"><a class="ui green tiny button" v-if="isUser(uid) &amp;&amp; !isMember(groups[$route.params.idx].idx)" @click="join(groups[$route.params.idx].idx)">{{$t('groups.join_group')}}</a><a class="ui red tiny button" v-if="isUser(uid) &amp;&amp; isMember(groups[$route.params.idx].idx)" @click="out(groups[$route.params.idx].idx)">{{$t('groups.out_group')}}</a></span></p></div><div class="two column stackable row"><div class="column"><div class="ui divided list center aligned"><div class="item center aligned">{{$t('group.resources')}}</div><div class="item center aligned" v-for="(r, index) in sortedResources" :key="index + r.n + r.href" v-show="!r.hidden || edit"><div class="resource-content"><a :href="r.href" target="_blank" rel="noopener noreferrer"><img :src="'http://www.google.com/s2/favicons?domain=' + r.href" :alt="r.n">{{r.n}}</a><div class="filler"></div></div><div class="resource-buttons"><a class="ui basic green button" v-if="edit &amp;&amp; r.hidden" @click="showResource($route.params.idx, index)"><i class="eye icon"></i>{{$t('group.show_resource')}}</a><a class="ui basic red button" v-if="edit &amp;&amp; !r.hidden" @click="hideResource($route.params.idx, index)"><i class="hide icon"></i>{{$t('group.hide_resource')}}</a><a class="ui blue button" v-if="isUser(uid) &amp;&amp; !(r.likes || []).includes(uid)" v-show="!edit" @click="likeResource($route.params.idx, index)"><i class="thumbs up icon"></i>{{$t('group.like')}}</a><span class="red" v-if="r.likes &amp;&amp; r.likes.length &gt; 0"><i class="heart icon"></i>{{r.likes.length}} {{$t('group.likes')}}</span></div></div><div class="item ui form v-show">uid<div class="ui three stackable fields"><div class="field no-margin no-padding"><div class="ui labeled input"><label class="ui label">{{$t('group.enter_resource')}}</label><input type="text" v-model="newResName" @input="filterInput('newResName', $event)" :placeholder="$t('group.enter_resource_first')" style="width: 100%; font-size: 20px; padding: 15px;"></div></div><div class="field no-margin no-padding"><div class="ui labeled input"><label class="ui label">{{$t('group.enter_link')}}</label><input type="text" v-model="newHref" @input="filterInput('newHref', $event)" :placeholder="$t('group.enter_link_first')" style="width: 100%; font-size: 20px; padding: 15px;"></div></div><div class="field no-padding"><a class="ui green button" :class="{disabled: !newHref || !newResName}" @click="addRes($route.params.idx)">{{$t('group.add_resource')}}</a></div></div></div></div></div><div class="column center aligned">{{ $t('login.leave_messages') }}<div class="ui divided list left aligned"><div class="item" v-for="(c, index) in latestChats" :key="index"><img class="ui avatar" :src="c.photoURL">{{c.n}} : {{c.t}}</div><div class="item ui form" v-if="isMember(groups[$route.params.idx].idx)"><div class="field"><div class="ui labeled input"><img class="ui avatar" :src="photoURL"><input class="input" v-model="msg" @input="filterInput('msg', $event)" :placeholder="$t('group.anything_to_say')" style="font-size: 18px; padding: 10px;"><a class="ui label green button" :class="{disabled: !msg}" @click="addChat($route.params.idx)">{{ $t('login.leave_messages') }}</a></div></div></div></div></div></div></div></div></div></div></loader></div></div>